<template>
	<div class="header">
		<img class="back" v-if='title !== ""' @click="goback()" src="../../../static/icon-back.png" alt="">
		<article class="txt">{{title}}</article>
	</div>
</template>	
<script>
	export default{
		name:"Header",
		methods:{
			goback(){
				this.$router.back()
			}
		},
		created(){
			console.log(this.$route)
		},
		computed: {
      		title () {
		        switch (this.$route.path.split('/')[1]) {
		          case 'Home':
		            return ''
		          case 'Detail':
		            return ''
		          case 'Invite':
		            return ''
		          case 'Org':
		            return ''
		          case 'actDate':
		            return ''
		          case 'Enrol':
		            return ''
		          case 'detail':
		            return this.deTitle
		        }
	      }
    	}
	}
</script>
<style lang="stylus">
	.header
		position fixed
		width 100%
		height 48px
		background #f60
		display flex
		align-items center
		justify-content center
		color #fff
		z-index 1
		.txt
			height 20px
		.back
			position fixed
			width 20px
			heigth 20px
			left 10px
			top 14px	

</style>	


