<template>
	<div class="detail">
		<v-header></v-header>
		<section class="detail-box">
			<section class="intro">
				<header class="tit">{{item.name}}</header>
				<article>
					<div class="int-list">
						<img src="" alt="">
						<span>:</span>
						<span>{{item.date}}</span>
					</div>
					<div class="int-list">
						<img src="" alt="">
						<span>:</span>
						<span>{{item.address}}</span>
					</div>
					<div :class="{'active':isActive}" class="int-list">
						<img src="" alt="">
						<span>:</span>
						<span>
							<i class="f60"></i>
							<i class="f60">-</i>
							<i class="f60"></i>
							<i>-</i>
							<i></i>
						</span>
					</div>
					<div :class="{'active':isActive}" class="unit int-list">
						<div>
							<img src="" alt="">
							<span>:</span>
						</div>
						<ul>
							<li></li>
							<li></li>
							<li></li>
						</ul>
					</div>
					<div class="toggle-btn">
						<img @click="toggle" :src="isActive ? a : b" alt="">
					</div>
				</article>
			</section>
			<section class="latex">
				<div class="latex-col">
					<div class="latex-box latex-l" @click="goInvite()">
						<span class="round"><img src="../../../static/intro.png" alt=""></span>
						<span class="txt"></span>
					</div>
					<div class="latex-box" @click="goOrg()">
						<span class="round"><img src="../../../static/institution.png" alt=""></span>
						<span class="txt"></span>
					</div>
					<div class="latex-box latex-r" @click="goDate()">
						<span class="round"><img src="../../../static/institution.png" alt=""></span>
						<span class="txt"></span>
					</div class="latex-box">
				</div>
				<div class="latex-col">
					<div class="latex-box latex-l">
						<span class="round"><img src="../../../static/intro.png" alt=""></span>
						<span class="txt"></span>
					</div>
					<div class="latex-box">
						<span class="round"><img src="../../../static/fingerpost.png" alt=""></span>
						<span class="txt"></span>
					</div>
					<div class="latex-box latex-r">
						<span class="round"><img src="../../../static/intro.png" alt=""></span>
						<span class="txt"></span>
					</div class="latex-box">
				</div>	
			</section>
			<section class="fnav">
				<section class="fnav-list">
					<div>
						<img src="" alt="">
						<span></span>
					</div>
					<div>></div>
				</section>
				<section class="fnav-list">
					<div>
						<img src="" alt="">
						<span></span>
					</div>
					<div>></div>
				</section>
				<section class="fnav-list">
					<div>
						<img src="" alt="">
						<span></span>
					</div>
					<div>></div>
				</section>
				<section class="fnav-list" @click="goEnrol()">
					<div>
						<img src="" alt="">
						<span></span>
					</div>
					<div>></div>
				</section>
			</section>
		</section>
	</div>
</template>
<script>
	import Header from '@/components/Header/Header.vue'
	import router from '../../router/index'
	
	export default{
		name:"Detail",
		data () {
            return {
                isActive: true,
            	a:require('../../../static/downward.png'),
            	b:require('../../../static/upward.png'),
            	title:"",
            	item:this.$store.state.getMessDetail
            }
        },
        created(){
        	this.init();
        },
		methods:{
			init(){
				this.item=this.$store.state.getMessDetail
			},
			toggle(){
				this.isActive=!this.isActive
			},
			goInvite(){
				router.push({ name:'Invite', params:{ id:2 }})
			},
			goOrg(){
				router.push({ name:'Org', params:{ id:2 }})
			},
			goDate(){
				router.push({ name:'actDate', params:{ id:2 }})
			},
			goEnrol(){
				router.push({ name:'Enrol', params:{ id:2 }})
			}
		},
		// computed:{
		// 	item(){
		// 		return this.$store.state.getMessDetail
		// 	}
		// },
		components:{
			"v-header":Header
		}
		
	}
</script>
<style lang="stylus" scoped>
	.active
		display none
	.f60
		color #f60
	.detail-box
		position fixed
		top 48px
		width 92%
		padding 0 4%
		bottom 1px
		overflow-y auto
		.intro
			width 100%
			
			font-size 16px
			.tit
				font-size 18px
				margin-top 10px
			.int-list
				display flex
				margin-top 10px
			.unit
				display flex
			.active
				display none
			.toggle-btn
				display flex
				align-items center
				justify-content center
				img
					width 20px
					margin 10px 0
		.latex
			display flex
			flex-direction column
			.latex-col
				display flex
				justify-content space-between
				.latex-box
					display flex
					flex 1
					flex-direction column
					align-items center
					justify-content center
					margin 10px 0
					.round
						width 60px
						height 60px
						background #f60
						display flex
						align-items center
						justify-content center
						border-radius 50%
						img
							width 25px
							height 25px
				.latex-l
					justify-content left
				.latex-r
					justify-content right
		.fnav
			.fnav-list
				display flex
				justify-content space-between
				align-items center
				height 40px
				

							
					
						
					
					
					


</style>


