<template>
	<div class="orgDetail">
		<section class="box">
			<section class="org">
				<article class="org-unit">
					<header class="org-tit">
						<span class="org-line"></span>
						<span class="org-txt"></span>
					</header>
					<section class="org-list">
						<div class="org-name"></div>
						<div class="org-name"></div>
						<div class="org-name"></div>
					</section>
				</article>
			</section>
			<section class="org-instruction">
				
			</section>
		</section>
	</div>
</template>
<script>
	export default{
		name:"OrgDetail",
		components:{
			
		}
	}
</script>
<style scoped >
.box{
	width:100%;
}
.org{
	width:96%;
	padding:0 2%;
	display: flex;
	flex-direction:column;
}
.org-unit{
	width:96%;
	padding:0 2%;
	background:#fff;
	border-radius:10px;
	box-shadow:0px 2px 4px #e0dcdb; 
	border-radius:10px;
	margin-top:5px;
}
.org-unit .org-tit{
	margin:10px 0;
	display:flex;
}
.org-unit .org-tit .org-txt{
	margin-left:10px;
}
.org-unit .org-tit .org-line{
	width:5px;
	border-radius:3px;
	background:#f9752b;
}
.org-unit .org-name{
	margin:10px 0;
}
.org-list{
	margin-left:15px;
}
.org-instruction{
	width:92%;
	padding:0 3%;
	font-size: 12px;
	margin-top:20px;
	color:#b4b4b4;
	line-height: 24px;
}
</style>


