// mockjs
const Mock = require('mockjs');
//  mock.Random 
const Random = Mock.Random;
// mock
const produceNewsData = function() {
	let articles = [];
	for (let i = 0; i < 10; i++) {
		let newArticleObject = {
			id: Random.integer(1, 10),
			name: Random.csentence(5, 30), //  Random.csentence( min, max )
			thumbnail_pic_s: Random.dataImage('300x250', 'mock'), // Random.dataImage( size, text )  Base64 
			address: Random.city(0, 5), // Random.cname() 
			date: Random.date() // Random.date(),yyyy-MM-ddRandom.time() 
		}
		articles.push(newArticleObject)
	}

	return {
		articles: articles
	}
}
const date = function() {
		let articles = [];
		for (let i = 0; i < 10; i++) {
			let newArticleObject = {
				id: Random.integer(1, 10),
				name: Random.csentence(5, 30), //  Random.csentence( min, max )
				thumbnail_pic_s: Random.dataImage('300x250', 'mock'), // Random.dataImage( size, text )  Base64 
				address: Random.city(0, 5), // Random.cname() 
				date: Random.date() // Random.date(),yyyy-MM-ddRandom.time() 
			}
			articles.push(newArticleObject)
		}

		return {
			articles: articles
		}
	}
	// Mock.mock( url, post/get , )
Mock.mock('/news/index', 'post', produceNewsData);
Mock.mock('/aa', 'get', date);


